import React from "react";
import classes from "./Feedback.module.css";

let Feedback = (props) => {
    return (
        <div className={classes.Feedback}>
            <div className={classes.headingBlock}>
                <h2 className={classes.headingText}>
                             ?
                </h2>
                <h2 className={classes.writeBack}>
                     !
                </h2>
            </div>
            <form onSubmit={props.submit} className={classes.form}>
                <div className={classes.contacts}>
                    <label>
                        :
                        <input name={"name"} type={"text"} className={classes.input} placeholder={""}/>
                    </label>
                    <label>
                        :
                        <input name={"phone"} type={"phone"} className={classes.input} placeholder={"+7"}/>
                    </label>
                </div>
                <div className={classes.textAreaWrapper}>
                    <label className={classes.label}>
                        :
                        <textarea placeholder={"  ..."} className={classes.input + " " + classes.textArea}/>
                    </label>
                </div>
                <div className={classes.btnWrapper}>
                    <button type={"submit"} className={classes.submitBtn}></button>
                </div>
            </form>
        </div>
    )
}

export default Feedback;